C3JA;AD6JA;EACA,kBAAA;AC3JA","file":"Login.vue","sourcesContent":["<i18n>\n{\n  \"en\": {\n    \"login\": \"Login\",\n    \"forget\": \"Forget password\",\n    \"accountTip\": \"Please enter your account\",\n    \"passwordTip\": \"Please enter your password\",\n    \"validAccountTip\": \"Please enter valid account\",\n    \"validPasswordTip\": \"Please enter valid password\",\n    \"captchaBtn\": \"I'm not a robot\",\n    \"captchaSuccess\": \"Succeed\",\n    \"captchaFail\": \"Failed, click to refresh\",\n    \"captchaScan\": \"Scanning\",\n    \"useEmail\": \"Switch to email\",\n    \"usePhone\": \"Switch to mobile\"\n  },\n  \"zh\": {\n    \"login\": \"登录\",\n    \"forget\": \"忘记密码\",\n    \"accountTip\": \"请输入账号\",\n    \"passwordTip\": \"请输入密码\",\n    \"validAccountTip\": \"请输入正确的账号\",\n    \"validPasswordTip\": \"请输入正确密码\",\n    \"captchaBtn\": \"点击按钮开始智能验证\",\n    \"captchaSuccess\": \"验证成功\",\n    \"captchaFail\": \"验证失败，请在此点击按钮刷新\",\n    \"captchaScan\": \"智能检测中\",\n    \"useEmail\": \"切换邮箱登录\",\n    \"usePhone\": \"切换手机号登录\"\n  },\n  \"ja\": {\n    \"login\": \"ログイン\",\n    \"forget\": \"パスワードを忘れた場合\",\n    \"accountTip\": \"メールアドレスまたは携帯電話番号を入力してください\",\n    \"passwordTip\": \"パスワードを入力してください\",\n    \"validAccountTip\": \"正しいメールアドレスまたは携帯電話番号を入力してください\",\n    \"validPasswordTip\": \"正しいパスワードを入力してください\",\n    \"captchaBtn\": \"私はロボットではありません\",\n    \"captchaSuccess\": \"成功しました\",\n    \"captchaFail\": \"失敗しました。クリックしてもう一度やり直してください\",\n    \"captchaScan\": \"承認中\",\n    \"useEmail\": \"メールアドレスログイン\",\n    \"usePhone\": \"電話番号ログイン\"\n  }\n}\n</i18n>\n\n<template>\n  <div class=\"login-container\" v-if=\"show\">\n    <form>\n      <el-row class=\"form-group text-left\">\n        <label class=\"control-label\" :class=\"{'error':inputFocus == 'account'}\">\n          {{account_tips}}\n          <a\n            class=\"toggle\"\n            href=\"javascript:;\"\n            @click=\"phoneInput = !phoneInput\"\n          >{{ toggleText }}</a>\n        </label>\n        <vue-tel-input placeholder @input=\"onMobileInput\" v-if=\"phoneInput\"></vue-tel-input>\n        <input v-model=\"account\" type=\"text\" class=\"form-control\" v-else />\n      </el-row>\n      <el-row class=\"form-group text-left\">\n        <label class=\"control-label\" :class=\"{'error':inputFocus == 'password'}\">{{password_tips}}</label>\n        <input v-model=\"password\" type=\"password\" class=\"form-control\" />\n      </el-row>\n      <el-row class=\"login-button\">\n        <el-button\n          type=\"primary\"\n          class=\"btn btn-primary\"\n          :loading=\"submitting\"\n          @click.prevent=\"confirm\"\n        >{{ $t(\"login\") }}</el-button>\n      </el-row>\n      <el-row class=\"text-center\">\n        <a class=\"link\" href=\"javascript:;\" @click=\"forgot\">{{ $t(\"forget\") }}</a>\n      </el-row>\n    </form>\n  </div>\n</template>\n<script>\nexport default {\n  name: \"Login\",\n  props: {\n    show: {\n      type: Boolean,\n      default() {\n        return false;\n      }\n    }\n  },\n  data() {\n    return {\n      submitting: false,\n      inputFocus: \"\",\n      account: \"\",\n      international_code: 0,\n      password: \"\",\n      account_tips: this.$t(\"accountTip\"),\n      password_tips: this.$t(\"passwordTip\"),\n      phoneInput: true\n    };\n  },\n  computed: {\n    toggleText() {\n      return this.phoneInput ? this.$t(\"useEmail\") : this.$t(\"usePhone\");\n    }\n  },\n  methods: {\n    validate() {\n      let { isPhoneValid, password, phoneInput } = this;\n      if (phoneInput && !isPhoneValid) {\n        this.account_tips = this.$t(\"validAccountTip\");\n        this.inputFocus = \"account\";\n        return false;\n      }\n\n      this.account_tips = this.$t(\"accountTip\");\n\n      if (!password) {\n        this.password_tips = this.$t(\"passwordTip\");\n        this.inputFocus = \"password\";\n        return false;\n      }\n      this.inputFocus = \"\";\n      return true;\n    },\n    async confirm() {\n      if (!this.validate()) {\n        console.log(\"验证失败！\");\n        return;\n      }\n      this.submitting = true;\n      await this.$store\n        .dispatch(\"login\", {\n          account: this.account,\n          international_code: this.international_code,\n          password: this.password\n        })\n        .catch(e => {\n          console.log(e);\n        });\n      this.submitting = false;\n    },\n    onMobileInput(formattedNumber, { number, valid, country }) {\n      this.account = number.significant;\n      this.international_code = parseInt(country.dialCode);\n      this.isPhoneValid = valid;\n    },\n    forgot() {\n      require(\"electron\").shell.openExternal(\n        `https://mpool.net/?lang=` + this.$root.$i18n.locale\n      );\n    }\n  }\n};\n</script>\n<style scoped>\n.login-container {\n  margin-top: 61px;\n}\n.toggle {\n  margin: 0 20px;\n}\n.control-label {\n  display: block;\n  height: 12px;\n  font-size: 10px;\n  font-weight: normal;\n  color: #042161;\n  line-height: 12px;\n  margin-top: 15px;\n}\n.control-label.error {\n  color: red;\n}\n.form-control {\n  display: block;\n  width: 100%;\n  padding: 5px 0;\n  line-height: 1.5;\n}\n.el-autocomplete.form-control {\n  border-bottom: none;\n}\ninput {\n  outline: none;\n  box-shadow: none;\n  border: none;\n  border-radius: 0;\n  border-bottom: 1px solid rgba(4, 33, 97, 0.3);\n  font-size: 14px;\n  font-weight: 400;\n  color: #000000;\n}\n.small_title {\n  font-size: 12px;\n  font-weight: 400;\n  color: rgba(0, 0, 0, 0.4);\n  line-height: 12px;\n}\n.link {\n  font-size: 12px;\n  font-weight: 400;\n  color: rgba(33, 69, 219, 1);\n  line-height: 17px;\n  margin-top: 10px;\n  text-decoration: none;\n  cursor: pointer;\n}\n.btn-primary {\n  font-size: 12px;\n  font-weight: bold;\n  color: rgba(255, 255, 255, 1);\n  width: 100%;\n  border: none;\n}\n.login-button {\n  padding-top: 123px;\n}\n</style>\n\n<style>\n.el-input__inner {\n  display: block;\n  width: 100%;\n  paddi